.right-sidebar{
  width: calc(100% - 263px);
}
.choose {
  border-bottom: 1px solid #ecedf0;
  padding: 41px 48px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #fff;
  z-index: 99; 
  .title-choose {
    margin: 0;
    color: #3c3e49;
    font-size: 28px;
    display: flex;
    position: absolute;
    left: 48px;
  }
  .choose-list {
    position: absolute;
    display: flex;
    .search-btn{
      width: 38px;
      height: 38px;
      border-radius: 3px;
      border: 1px solid #dfe0e6;
    }
    .icon-search {
      width:100%;
    }
    .btn-item {
      border-radius: 3px;
      font-size: 13px;
      line-height: 20px;
      min-height: 40px;
      padding: 0 16px;
      color: #3c3e49;
      border: 1px solid #dfe0e6;
      margin-right: 12px;
      margin-left: 12px;
    }
    .choose-img{
      width:20px;
      margin: -5px 8px -5px 0;
    }
    .arrange {
      width:40px;
      height: 40px;
      border-radius: 3px;
      border: 1px solid #dfe0e6;
      .arrange-img {
        width:100%;
      }
    }
  }
}
